body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
input,
button,
dl,
dt,
dd {
    margin: 0;
    padding: 0;
}

ol,
ul {
    list-style: none;
}

a {
    text-decoration: none;
}

body {
    font-family: 'Nanum Gothic', sans-serif;
    color: $흰색;
}

.main_header {
    width: $너비;
    margin: auto;
    display: flex;
    @extend .height;

    .h1_logo {
        flex: 1;
        padding-top: 3px;
        margin-right: 10px;

        a {
            display: block;
            width: 103px;
            height: 25px;

            img {
                width: 100%;
                vertical-align: middle;
            }
        }
    }

    .navlist1 {
        flex: 7;
        display: flex;
        font-size: $작은폰트;
        @include 라인하이트();

        li {
            letter-spacing: -1.5px;

            &:not(:last-child) {
                padding-right: 12px;
                background: url('./header_line_bg.gif') no-repeat 94% 20px;
            }

            a {
                color: $진회색;
            }
        }
    }

    .navlist2 {
        flex: 2;
        display: flex;
        justify-content: end;
        font-size: $작은폰트;
        @include 라인하이트();

        li {
            padding-left: 14px;
            background: url('./header_middleLine_bg.gif') no-repeat left 24% center;

            a {
                display: block;
                width: 100%;
                color: $진회색;
                padding-left: 6px;
                letter-spacing: -2px;
            }
        }
    }
}

.mainbanner_bg {
    background: url('./main_bg.png') no-repeat;
    background-position: center top;
    margin: auto;
    min-height: 100vh;
    position: relative;

    .mainbanner_nav {
        width: $너비;
        margin: auto;
        display: flex;
        justify-content: flex-end;
        padding-top: 20px;
        padding-right: 30px;
        font-size: $작은폰트;
        font-weight: bold;

        li {
            padding-left: 20px;

            a {
                color: $흰색;
            }
        }
    }

    .banner_contents {
        display: flex;
        width: $너비;
        margin: auto;

        .mainbanner_h2 {
            padding-left: 66px;
            padding-top: 7px;
            margin-right: 40px;
        }

        .banner_form {
            width: 464px;
            height: 50px;
            border: 1px solid #dbf8fd;
            border-radius: 4px;
            margin-top: 50px;

            input {
                background: transparent;
                width: 88%;
                height: 50px;
                border: 0 none;
                vertical-align: top;
                padding-left: 10px;
                box-sizing: border-box;
                color: #fff;
                font-size: $큰폰트;
                font-weight: bold;
            }

            button {
                background: transparent;
                width: 12%;
                height: 50px;
                border: 0 none;
                cursor: pointer;
            }
        }

        .mainbanner_list {
            width: 90px;
            height: 50px;
            margin-left: 10px;
            margin-top: 50px;
            cursor: pointer;
            background: transparent;
            border: 0 none;
        }
    }
}

.listBox {
    position: absolute;
    left: 50%;
    top: 140px;
    transform: translateX(-50%);
    width: 993px;
    height: 582px;

    .tabbtn {
        position: absolute;
        left: 50%;
        top: 24px;
        transform: translateX(-50%);
        margin-bottom: 100px;
        display: flex;


        button {
            border: 0 none;
            color: $흰색;
            width: 160px;
            height: 36px;
            background-color: transparent;
            margin-right: 6px;
        }

        .tab1 {
            background-image: url('./img/listView_Tab1.png');

            &:focus {
                background-image: url('./img/listView_Tab1_on.png');
                height: 44px;
            }
        }

        .tab2 {
            background-image: url('./img/listView_Tab2.png');
            margin-right: 0;

            &:focus {
                background-image: url('./img/listView_Tab2_on.png');
                height: 44px;
            }
        }
    }

    .closebtn {
        position: absolute;
        top: 30px;
        right: 20px;
        background: transparent;
        border: 0 none;
    }
}

.region {
    @include 지역별테마별();

    div {
        flex: 1 1 0;
    }

    h3 {
        @include h3(80%);

        span {
            font-style: italic;
            font-weight: lighter;
            padding-left: 4px;
            opacity: 0.7;
        }
    }

    dl {
        padding: 10px;
        box-sizing: border-box;
        background: rgba(255, 255, 255, 0.7);

        dt {
            border-bottom: 1px solid $파랑색;
            padding: 2px 10px 9px 10px;
            color: $파랑색;
            font-size: $중간폰트;
            font-weight: 900;
        }

        dd {
            font-size: $작은폰트;
            padding: 5px 0 7px 0;
            letter-spacing: -1px;

            ul {
                display: flex;
                flex-wrap: wrap;

                li {
                    width: 50%;
                    background: url('./cmn_square_greyblet.gif') no-repeat left center;
                    padding-left: 10px;
                    box-sizing: border-box;
                    margin-bottom: 4px;

                    a {
                        color: $검정색;
                    }
                }

                .last {
                    width: 100%;
                }
            }
        }
    }

    .japan {
        margin-bottom: 50px;


    }

    .w100>li {
        width: 100%;
    }

}

.theme {
    @include 지역별테마별();

    div {
        flex: 1 1 0;

        h3 {
            @include h3(60%);
        }

        ul {
            padding: 10px;
            box-sizing: border-box;
            background: rgba(255, 255, 255, 0.7);
            font-size: $작은폰트;

            li {
                background: url('./cmn_square_greyblet.gif') no-repeat left center;
                padding-left: 10px;
                box-sizing: border-box;
                margin-bottom: 20px;
                line-height: 1.4;

                a {
                    color: $파랑색;
                }
            }
        }
    }
}

.grid_container {
    width: 980px;
    height: 753px;
    margin: 10px auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 4px;

    >div {
        position: relative;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        a {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            text-align: center;

            .off {
                @include 어쩌구();
                font-size: $큰폰트 + 8px;
                display: none;
            }

            .on {
                width: 100%;
                height: 100%;
                padding: 4px;
                box-sizing: border-box;
                background: rgba(0, 0, 0, 0.4);

                .borderbox {
                    @include 어쩌구();
                    flex-direction: column;
                    border: 1px solid rgba(255, 255, 255, 0.4);
                    width: calc(100% - 2px);
                    height: calc(100% - 2px);

                    strong {
                        font-weight: 900;
                        font-size: $큰폰트 + 8px;
                        color: $연두색;
                        margin-bottom: 6px;
                    }

                    span {
                        display: block;
                        width: 70px;
                        height: 70px;
                        border-radius: 35px;
                    }
                }
            }
        }
    }
}